html {
  height: 100%;
}

body {
  height: 100%;
  font-family: sans-serif;
}

.container {
  text-align: center;
  height: 100%;
  background: #d3d3d3;
  padding: 16px;
}

#myTargetedIframe {
  width: 70%;
  height: 300px;
  border: none;
  overflow-y: hidden;
  box-shadow: -1px -1px 4px #000;
}

.text {
  padding-bottom: 16px;
}

.explanation {
  background: #ddd;
  padding: 8px 16px 16px;
}

.explanation-text, .explanation:focus .explanation-tooltip {
  display: none;
}

.explanation:focus .explanation-text {
  display: block;
}

#log {
  height: 10em;
  background: #bbb;
  padding: 16px;
  display: block;
  overflow-y: scroll;
}

#log li {
  background: #ccc;
  margin-bottom: 8px;
  padding: 8px 0;
  display: block;
}

#log li.success {
  background: #90ee90;
}

#log li:last-child {
  margin-bottom: 0;
}

#focusStatus {
  background: #f08080;
  margin: 8px 0;
  padding: 16px;
  display: block;
}

#focusStatus.focused {
  background: #90ee90;
}

