html {
  height: 100%;
}

body {
  font-family: sans-serif;
  height: 100%;
}

.container {
  background: lightgrey;
  text-align: center;
  padding: 16px;
  height: 100%;
}

#myTargetedIframe {
  border: none;
  box-shadow: -1px -1px 4px black;
  width: 70%;
  height: 300px;
  overflow-y: hidden;
}

.text {
  padding-bottom: 16px;
}

.explanation {
  background: #ddd;
  padding: 8px 16px 16px;
}

.explanation-text {
  display: none;
}

.explanation:focus .explanation-tooltip {
  display: none;
}

.explanation:focus .explanation-text {
  display: block;
}

#log {
  padding: 16px;
  background: #bbb;
  display: block;
  height: 10em;
  overflow-y: scroll;
}

#log li {
  display: block;
  background: #ccc;
  padding: 8px 0;
  margin-bottom: 8px;
}

#log li.success {
  background: lightgreen;
}

#log li:last-child {
  margin-bottom: 0;
}

#focusStatus {
  display: block;
  margin: 8px 0;
  padding: 16px;
  background: lightcoral;
}

#focusStatus.focused {
  background: lightgreen;
}
